<template>
  <div class="dashboard">
    <db-nav-drawer
      :show="showNav"
      @show="showNav = $event"
      @toggleFilter="toggle($event)"
      :filters="filters"
    />
    <db-header
      @show="showNav = !showNav"
      :search="search"
      @search="search = $event"
      :loading="loading"
      :filters="filters"
      @refresh="getSources()"
    />
    <db-list 
      :search="search"
      :filters="filters"
      :items="items"
      :action="item => $router.push('/' + item.className + '/' + item.objectId)"
    />
    <add-modal/>
  </div>
</template>

<script>
import Header from "@/components/dashboard/Header.vue"
import List from "@/components/dashboard/List.vue"
import NavDrawer from "@/components/dashboard/NavDrawer.vue"
import AddModal from "@/components/dashboard/AddModal.vue"


export default {
  components: {
    "db-header" : Header,
    "db-list" : List,
    "db-nav-drawer" : NavDrawer,
    AddModal,
  },
  data() {
    return {
      items: null,
      showNav: false,
      search: "",
      loading: false,
      filters: {
        "ingredients" : {text: 'Hide Ingredients', status: false},
        "recipes" : {text: 'Hide Recipes', status: false},
        "shared" : {text: 'Hide Shared items', status: false}
      }
    }
  },
  created() {
    this.getSources()
  },
  methods: {
    getSources() {
      this.loading = true
      this.$Parse.Cloud.run("get_sources", {}).then(response => {
        console.log(response)
        if (response.status == "success") {
          this.items = this.sourceSetup(response.data)
          console.log(this.items)
        } else {
          console.log("Errored response")
        }
        this.loading = false
      }, error => {
        this.loading = false
        console.error(error)
      }); 
    },
    toggle(filter) {
      this.filters[filter].status = !this.filters[filter].status
    },
    sourceSetup(response) {
      let typing = (x, type) => {
        return {
          ...x,
          className: type
        };
      }
      return {
        ingredients: response.ingredients.map(x => typing(x.toJSON(), "ingredient")),
        recipes: response.recipes.map(x => typing(x.toJSON(), "recipe"))
      }
    }
  }
}

</script>

<style scoped>
.dashboard {
  height: 100%
}
</style>