<template>
  <v-navigation-drawer
    v-model="showNav"
    clipped
    app
  >
    <v-toolbar flat>
      <v-list>
        <v-list-tile>
          <v-list-tile-title class="title">
            Filters
          </v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-toolbar>
    <v-list>
      <v-list-tile
        v-for="filter in Object.entries(filters)"
        :key="filter[0]"
      >
        <v-list-tile-content>
          <v-list-tile-title>{{filter[1].text}}</v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-switch color="primary" :value="filter[1].status" @click.stop="$emit('toggleFilter', filter[0])" readonly/>
        </v-list-tile-action>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  export default {
    props: [
      "show",
      "filters"
    ],
    computed: {
      showNav: {
        get() {
          return this.show
        },
        set(val) {
          this.$emit("show", val)
        }
      },
    }
  }
</script>