<template>
  <v-dialog v-model="dialog" max-width="500px">
    <v-btn
      color="primary"
      dark
      fixed
      bottom
      right
      fab
      slot="activator"
      @click="dialog = true"
    >
      <v-icon>add</v-icon>
    </v-btn>
    <v-card>
      <v-card-title class="headline pb-0">
        New Item
      </v-card-title>
      <v-card-text>
        <v-radio-group v-model="type" row required>
          <v-radio label="Ingredient" value="ingredient" color="primary"/>
          <v-radio label="Recipe" value="recipe" color="primary"/>
          <v-spacer/>
          <v-icon v-if="type == 'recipe'">
            restaurant
          </v-icon>
          <v-icon v-if="type == 'ingredient'">
            pie_chart
          </v-icon>
        </v-radio-group>
        <v-text-field
          v-model="name" 
          label="Name"
          hint="Required. Can be modified later."
          required
        />
        <v-textarea
          v-model="description" 
          label="Description"
          auto-grow
        />
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="dialog = false">Cancel</v-btn>
        <v-btn color="primary" flat @click.native="addItem()" :loading="loading">Add</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        type: null,
        name: null,
        description: null,
        loading: false,
      }
    },
    methods: {
      addItem() {
        console.log("Creating new item...")
        // Needs form validation
        this.loading = true
        let query = this.type == "ingredient" ? "post_ingredient" : "post_recipe"
        console.log(query)
        console.log(this.name)
        console.log(this.description)
        this.$Parse.Cloud.run(query, {name: this.name, description: this.description}).then(response => {
          console.log(response)
          this.dialog = false
          this.loading = false
          this.$router.push('/' + this.type + '/' + response.data.id)
        }, error => {
          console.error(error)
          this.loading = false
        });
      }
    }
  }
</script>