<template>
  <div>
    <v-layout row align-center class="pt-2 px-3">
      <v-tooltip bottom>
        <v-btn
          icon
          class="mr-3"
          @click="$emit('show')"
          slot="activator"
        >
          <v-icon>menu</v-icon>
        </v-btn>
        Filters
      </v-tooltip>
      <v-text-field
        v-model="searchString"
        append-icon="search"
        label="Search"
        :hint="Object.values(filters).some(x => x.status) ? 'Filters applied.' : ''"
        persistent-hint
      />
      <v-tooltip bottom>
        <v-btn
          icon
          slot="activator"
          @click="$emit('refresh')"
        >
          <v-icon>
            refresh
          </v-icon>
        </v-btn>
        Refresh
      </v-tooltip>
    </v-layout>
    <v-progress-linear 
      v-if="loading"
      :indeterminate="true" 
      class="ma-0"
    />
  </div>
</template>

<script>
import AddModal from '@/components/dashboard/AddModal.vue'

export default {
  components: {
    AddModal
  },
  props: [
    "search",
    "loading",
    "filters"
  ],
  computed: {
    searchString: {
      get() {
        return this.search
      },
      set(val) {
        this.$emit("search", val)
      }
    }
  }
}
</script>